import React, { useContext } from 'react';
import { UserContext } from './MyContext';

function GeoLocalisation () {   
    
    const {setQuery}=useContext(UserContext)       
    const {setResult}=useContext(UserContext)
    
    const api = {
        key : process.env.REACT_APP_TOKEN,
        base :"https://api.openweathermap.org/data/2.5/"
      }
   
    const api2 = {
        key : process.env.REACT_APP_TOKEN2,
        base :'https://geolocation-db.com/json/'
      }             

    const localise = ()=>{
        fetch(`${api2.base}${api2.key}`)
        
        .then((response)=>response.json())
        .then((data)=>{ 
            fetch(`${api.base}forecast?q=${data.city}&units=metric&appid=${api.key}&lang=fr`)
			.then((response)=>response.json())
            .then(async (dataJson) => {	                					
				setResult(await dataJson)					
				setQuery('')                            	
			})				
			.catch(console.error)       		              
        })        
        .catch (console.error)      }

            
    return(
        <div>
            <button className="btn btn-info btn-lg "
                onClick={localise}>
                    Météo de ma position
            </button>
            
        </div>
    )
}

export default GeoLocalisation